body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.cart-item {
  display: inline-flex;
  padding: 10px 10px;
  margin: 20px 10px;
  border: 0.5px solid black;
  border-radius: 10px;
}
.cart-item .right-block {
  padding: 0 20px;
}
.cart-item-actions {
  margin-top: 20px;
}
.action-icons {
  height: 25px;
  width: 25px;
  margin-right: 10px;
  display: inline-block;
}
.action-icons:hover {
  cursor: pointer;
}
.cart{
  display: flex;
  flex-direction: column;
}
.btn{
  font-size: 20px;
  font-weight: bold;
  box-shadow: 5px 5px 5px 5px black;
  border-radius: 20px;
}
.amt{
  padding: 20px;
  border-left: 10px solid #0597ff;
  background-color: #9ceeff;
}
